body {
  text-align: start;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0C1014;
  color: rgb(255, 255, 255);
  /* background: linear-gradient(to right, #FDFBFB, #EBEDEE 70%); */
}
*{
  text-align: start;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.oswald{
  font-family: 'Oswald', sans-serif;
  background: linear-gradient(to right, #2FB8FF 0%, #8FE4DD 100%);
  -webkit-background-clip: text;
          background-clip: text;
	-webkit-text-fill-color: transparent;
}

.orbitron{
  font-family: 'Orbitron', sans-serif;
  background: linear-gradient(to right, #D10955 0%, #FEBE54 100%);
  -webkit-background-clip: text;
          background-clip: text;
	-webkit-text-fill-color: transparent;
}

.knewave{
  font-family: 'Knewave', cursive;
  font-weight: 100;
  background: linear-gradient(to right, #F15E85 0%, #6572D8 100%);
  -webkit-background-clip: text;
          background-clip: text;
	-webkit-text-fill-color: transparent;
}

.monoton{
  font-family: 'Monoton', cursive;
  font-weight: 200;
  background: linear-gradient(to right, #A04FAB 0%, #EEB96D 100%);
  -webkit-background-clip: text;
          background-clip: text;
	-webkit-text-fill-color: transparent;
}

.press{
  font-family: 'Press Start 2P', cursive;
  background: linear-gradient(to right, #A04FAB 0%, #EEB96D 100%);
  -webkit-background-clip: text;
          background-clip: text;
	-webkit-text-fill-color: transparent;
}

.mainText{
  font-family: 'Inter', sans-serif;
  padding: 20px;
  position: absolute;
  top: 50vh;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
  margin-left: 20px;
}

.mainText h1{
  font-size: 80px;
  font-weight: 900;
  text-align: start;
  margin: 0%;
}
.mainText h2{
  font-size: 50px;
  font-weight: 900;
  text-align: start;
  margin: 0;

}

.light{
  font-weight: 200;
}

.versionbox{
  background-image: url(/portfolio/static/media/svgbox-01.f57e41e5.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 20px;
  margin: 20px;
  position: absolute;
  right: 0;
}

.versionbox h4{
  font-weight: 900;
  font-size: 20px;
}
.versionbox h5{
  font-weight: 100;
  font-size: 20px;
}

.versionbox h3{
  font-family: 'Rubik', sans-serif;
  font-style: italic;
  font-weight: 900;
  font-size: 30px;
}
