.icon-linkedin {
  background-image: url(/portfolio/static/media/linked-in-01.f9d17564.svg);
}
.icon-linkedin:hover,
.icon-linkedin:focus {
  background-image: url(/portfolio/static/media/linked-in-active-01.5ccbab40.svg);
}
.icon-github {
  background-image: url(/portfolio/static/media/github-01.84303808.svg);
}
.icon-github:hover,
.icon-github:focus {
  background-image: url(/portfolio/static/media/github-active-01.da77ec64.svg);
}
.icon-instagram {
  background-image: url(/portfolio/static/media/instagram-01.7f1f5160.svg);
}
.icon-instagram:hover,
.icon-instagram:focus {
  background-image: url(/portfolio/static/media/instagram-active-01.9f772933.svg);
}



@media (max-width: 992px) { 
  .mainText h1{
    font-size: 60px;
  }
  .mainText h2{
    font-size: 40px;

  }
  .versionbox h4{
    font-weight: 900;
    font-size: 15px;
  }
  .versionbox h5{
    font-weight: 100;
    font-size: 15px;
  }
  
  .versionbox h3{
    font-weight: 900;
    font-size: 25px;
  }

}

@media (max-width: 575.98px) {  
  .mainText h1{
    font-size: 40px;

  }
  .mainText h2{
    font-size: 25px;
  }
  .versionbox h4{
    font-weight: 900;
    font-size: 10px;
  }
  .versionbox h5{
    font-weight: 100;
    font-size: 10px;
  }
  
  .versionbox h3{
    font-weight: 900;
    font-size: 20px;
  }
}

@media (max-width: 400px) {  
  .mainText{
    font-family: 'Inter', sans-serif;
    padding: 10px;
    position: absolute;
    top: 50vh;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    margin-left: 10px;
  }
  .mainText h1{
    font-size: 30px;
  
  }
  .mainText h2{
    font-size: 20px;
  }
  .versionbox h4{
    font-weight: 900;
    font-size: 10px;
  }
  .versionbox h5{
    font-weight: 100;
    font-size: 10px;
  }
  
  .versionbox h3{
    font-weight: 900;
    font-size: 15px;
  }
  .icon {
    width: 20px;
    height: 20px;
    /* transition-timing-function: ease-out; */
  }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

